import { createStore } from 'vuex'

export default createStore({
  state: {
    "user":"111"
  },
  getters: {
  },
  mutations: {
    getuser(state,newValue){
      state.user = newValue
    }
  },
  actions: {
    setuser(context, value){
      // 修改getname的值
      context.commit('getuser',value)
    },
  },
  modules: {
  }
})
