<template>
<div class="page">

  <el-calendar v-model="value"  />

  <span style="font-size: 25px">当前时间: {{h}} 时 {{m}} 分 {{s}} 秒</span>
  <span style="margin-left: 100px">在线入住人数：{{person}}</span>
  <span style="margin-left: 180px; font-size: 18px" >房间剩余情况（{{homes}}间）</span>
  <el-carousel :interval="4000" type="card" height="200px">
    <el-carousel-item>
      <h3 text="2xl" justify="center">单人床({{dan}}间)</h3>
    </el-carousel-item>
    <el-carousel-item>
    <h3 text="2xl" justify="center">双人床({{shuang}}间)</h3>
    </el-carousel-item>
    <el-carousel-item>
    <h3 text="2xl" justify="center">豪华套间({{haohua}}间)</h3>
    </el-carousel-item>
    <el-carousel-item>
      <h3 text="2xl" justify="center">商务套间({{shangwu}}间)</h3>
    </el-carousel-item>
  </el-carousel>
</div>
</template>

<script>
import { ref,onMounted ,onBeforeMount} from 'vue'
import router from "@/router";
import API from "@/axios/axiosinstan";
export default {
  name: "Home",
  setup(){

    const value = ref(new Date())


     //时间数据
     let h = ref(new Date().getHours());
     let m = ref(new Date().getMinutes())
     let s = ref(new Date().getSeconds());



    //房间总数
    let homes = ref("")

    //房间总数
    let dan = ref("")

    //房间总数
    let shuang = ref("")

    //房间总数
    let haohua = ref("")

    //房间总数
    let shangwu = ref("")

    //总人数
    let person = ref("");


     let timer

     onMounted(()=>{

       //开启一个定时器更新时间
     timer =  setInterval(()=>{
           h.value = new Date().getHours()
          m.value = new Date().getMinutes()
          s.value = new Date().getSeconds();
       },1000)

       //获取人数，房间数量
       API(
           {
             url:"http://localhost:8081/home",
             methode:"get"
           }
       ).then((res)=>{
         console.log(res.data);
         person.value = res.data.persons

         homes.value = res.data.sumRooms;

         dan.value = res.data.danrens;

         shuang.value = res.data.shuangrens

         shangwu.value = res.data.shangwus;

         haohua.value = res.data.haohuas
       })

     })

    //销毁的时候关闭定时器
    onBeforeMount(()=>{
      clearInterval(timer)
    })


    return{
      value,h,m,s,person,homes,dan,shuang,shangwu,haohua
    }
  }
}
</script>

<style scoped>

.page{
  width: 100%;
  height: 900px;
  background-image: url("../image/3.jpg");
}

.el-carousel__item h3 {
  color: #475669;
  opacity: 0.75;
  line-height: 200px;
  margin: 0;
  text-align: center;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
</style>