<template>


  <span>账号:</span>&nbsp;&nbsp;&nbsp;
  <el-input
      v-model="user.account"
      placeholder="4到16位（字母，数字，下划线，减号）"
      style="width: 300px ;height: 40px"
      @mouseleave="setAccountMsg"
      @click="clickAccountMsg"
  />

  <p>{{msg.account}}</p>
  <br><br>


  <span >密码:</span>&nbsp;&nbsp;&nbsp;
  <el-input
      v-model="user.password"
      type="password"
      placeholder="4到16位（字母，数字，下划线，减号）"
      show-password
      style="width: 300px;height: 40px"
      @mouseleave="setPasswordMsg"
      @click="clickPassword"
  />
  <p>{{msg.password}}</p>
  <br><br>




  <el-button type="primary" round  style="width: 150px" @click="submit">提交</el-button>
  <el-button type="primary" round  style="width: 150px" @click="clearData">重置</el-button>

</template>

<script>
import {reactive, ref} from "vue";
import API from "@/axios/axiosinstan";
import {ElMessage } from "element-plus"
export default {
  name: "Register",
  setup(){
//加载是否显示
    //注册账户密码信息
    const user=reactive({
      password:"",
      account:""
    })

    //规则验证消息
    const msg = reactive(
        {
          password:"",
          account:""
        }
    )

    //当鼠标离开账号输入框
    function setAccountMsg(){
      //账号正则表达式
      var uPattern = /^[a-zA-Z0-9_-]{4,16}$/;
      if (! uPattern.test(user.account) && user.account!="")
      msg.account ="您输入的账号有误，请检查格式再次输入"
    }

    //当鼠标离开密码输入框
    function setPasswordMsg(){
      //密码正则表达式
      var uPattern = /^[a-zA-Z0-9_-]{4,16}$/;
      //当用户输入且不为空给出提示
      if (! uPattern.test(user.password) && user.password!="")
      msg.password="您输入的密码有误，请检查格式再次输入"
    }

    //当鼠标点击输入账号时，检测此时的msg,如果给出过提示则清空
    function clickAccountMsg(){
      if (  msg.account ="您输入的账号有误，请检查格式再次输入")
            msg.account = ""
    }

    //当鼠标点击输入密码时，检测此时的msg,如果给出过提示则清空
    function clickPassword (){
      if (msg.password ="您输入的账号有误，请检查格式再次输入")
        msg.password = ""
    }

    //当鼠标点击清空数据
    function clearData(){
      user.account = ''
      user.password=''
      msg.account = ''
      msg.password=''
    }

    //当鼠标点击提交
    function submit(){
      //查看此时账号密码是否符合规则,msg为空,密码不能为空
      if (msg.password=='' && msg.account=='' && user.password!="" && user.account!=""){
        API({
          url:"http://localhost:8081/zhuce",
                method:"post",
                data:user
        }).then(
            (res)=>{
              // 判断请求状态
                    console.log(res.data);
                    if (res.data.code=="成功"){
                      //注册成功
                        ElMessage("注册成功")
                      //清空
                      clearData()
                    }
                    else  ElMessage("注册失败，账号已存在")
            }
        )
      }else {
        ElMessage("请输入正确的账号密码")
      }


    }
    return{
      user,msg,setAccountMsg,setPasswordMsg,clickAccountMsg,clickPassword,clearData,submit
    }
  }
}
</script>

<style scoped>
p{
  color: red;
}
</style>