<template>
<div class="page">

    <el-form :model="form" label-width="120px" class="form"  >


      <el-form-item label="姓名" class="textinpu">
        <el-input v-model="form.name" />
      </el-form-item>
      <br>

      <el-form-item label="手机号" class="textinpu">
        <el-input v-model="form.phone" />
      </el-form-item>
      <br>

      <el-form-item label="身份证号" class="textinpu">
        <el-input v-model="form.idNo" />
      </el-form-item>
      <br>

      <el-form-item label="房间号" class="textinpu">
        <el-input v-model="form.roomNo" disabled />
      </el-form-item>
      <br>

      <el-form-item label="开房天数" class="textinpu">
        <el-input-number v-model="form.roomdays" :step="1" />
      </el-form-item>
      <br>


      <el-form-item label="房间类型">
        <el-select v-model="form.region" disabled="" placeholder="请选择">
          <el-option label="单人床" value="dna" />
          <el-option label="双人床" value="shuang" />
          <el-option label="商务套间" value="shangwu" />
          <el-option label="豪华套间" value="haohua" />
        </el-select>
      </el-form-item>
      <br>


      <el-form-item label="开始时间">
        <el-col :span="11">
          <el-date-picker
              v-model="form.startDay"
              type="date"
              placeholder="Pick a date"
              style="width: 100%"
          />
        </el-col>
        <el-col :span="2" class="text-center">
          <span class="text-gray-500">-</span>
        </el-col>
        <el-col :span="11">
          <el-time-picker
              v-model="form.house"
              placeholder="Pick a time"
              style="width: 100%"
          />
        </el-col>
      </el-form-item>
      <br>

      <el-form-item>
        <el-button type="primary" @click="onSubmit" style="width: 150px">提交订单</el-button>
        <el-button @click="reset">清空</el-button>
      </el-form-item>

    </el-form>

<p class="msg">剩余房间</p>

  <el-table :data="table.rooms"  @cell-click="clickcell"  style="width:755px" height="600px" class="tb"  >
    <el-table-column fixed prop="roomNo" label="房间号" width="250"  />
    <el-table-column prop="type" label="房间类型" width="250" />

    <el-table-column  fixed="right" label="Operations" width="120">
      <template #default>
        <el-button link type="primary" size="small" @click="handleClick"
        >选择此房间</el-button
        >
      </template>
    </el-table-column>
  </el-table>




</div>


</template>

<script>
import {onMounted, reactive} from "vue";
import API from "@/axios/axiosinstan";
import {ElMessage} from "element-plus";

export default {
  name: "Housing",
  setup(){


    //表单数据
    const form = reactive({
      name: '',
      phone: '',
      idNo: '',
      roomNo: '',
      roomdays:0,
      region:"",
      startDay:"",
      house:""
    })



    //清空
    function reset(){
          form.name= '',
          form.phone= '',
          form.idNo='',
          form.roomNo= '',
          form.roomdays=0,
          form.region="",
          form.startDay=""
          form.house = ""
    }

    //定义表格数据

const  table = reactive(
    {
      rooms:[]
    }
)

    //发送请求获取剩余房间
    function shengyufangjian(){
      API(
          {
            url:"http://localhost:8081/housing",
            method:"get",
          }
      ).then((res)=>{
        console.log(res.data);
        table.rooms = res.data
      })
    }


    //挂载完成后获取剩余房间信息
    onMounted(()=>{
      shengyufangjian()
    })


    //出发选择房间
   function clickcell(row, column, cell, event){
     form.roomNo = row.roomNo
     form.region = row.type
   }

   //提交订单
    function onSubmit(){
       if (form.name=="" || form.phone=="" ||form.idNo=="" ||form.roomNo=="" || form.startDay=="" ||form.region=="" || form.roomdays=="" || form.house==""){
         ElMessage("必须全部填写！！！！")
       }else{
         API({
           url:"http://localhost:8081/submitOrder",
           method:"post",
           data:form
         }).then((res)=>{
           //刷新剩余房间列表
           shengyufangjian()
           ElMessage("订单提交成功")
           reset();
         })
       }
    }

    return{
      form,table,clickcell,reset,onSubmit
    }
  }
}
</script>

<style scoped>

.page{
  width: 100%;
  height: 900px;
  font-size: 25px;
  background-image: url("../image/3.jpg");

}

.textinpu{
  width: 450px;
}

.form{
  width: 800px; margin-top: 150px;float:left;
  margin-left: 80px;
}
.tb{
  width: 450px;float:left;
  margin-top: 150px;
  margin-left: 50px;
}
.msg{
  position: absolute;
  margin-top: 100px;
  margin-left: 1300px;
  font-size: 20px;
}
</style>