<template>


  <span>账号:</span>&nbsp;&nbsp;&nbsp;
  <el-input
      v-model="user.account"
      placeholder="请输入账号"
      style="width: 300px ;height: 40px"
  />

  <br><br>


  <span >密码:</span>&nbsp;&nbsp;&nbsp;
  <el-input
      v-model="user.password"
      type="password"
      placeholder="4到16位（字母，数字，下划线，减号）"
      show-password
      style="width: 300px;height: 40px"
      @mouseleave="setPasswordMsg"
      @click="clickPassword"
  />
  <p>{{msg.password}}</p>
  <br><br>




  <el-button type="primary" round  style="width: 150px" @click="submit">修改</el-button>
  <el-button type="primary" round  style="width: 150px" @click="clearData">重置</el-button>

</template>

<script>
import {reactive, ref} from "vue";
import API from "@/axios/axiosinstan";
import {ElMessage } from "element-plus"
export default {
  name: "Modify",
  setup(){
//加载是否显示
    //修改账户密码信息
    const user=reactive({
      password:"",
      account:""
    })

    //规则验证消息
    const msg = reactive(
        {
          password:"",
        }
    )



    //当鼠标离开密码输入框
    function setPasswordMsg(){
      //密码正则表达式
      var uPattern = /^[a-zA-Z0-9_-]{4,16}$/;
      //当用户输入且不为空给出提示
      if (! uPattern.test(user.password) && user.password!="")
        msg.password="您输入的密码有误，请检查格式再次输入"
    }



    //当鼠标点击输入密码时，检测此时的msg,如果给出过提示则清空
    function clickPassword (){
      if (msg.password ="您输入的账号有误，请检查格式再次输入")
        msg.password = ""
    }

    //当鼠标点击清空数据
    function clearData(){
      user.account = ''
      user.password=''
    }

    //当鼠标点击修改
    function submit(){
      if (msg.password=="" && user.password!="" && user.account!=""){
        API(
            {
              url:"http://localhost:8081/xiugai",
              method:"post",
              data:user
            }
        ).then(
            (res)=>{
              //返送请求成功
              if (res.data.code=="成功"){
                ElMessage("修改成功")
                clearData()
              }else {
                ElMessage("请检查账号是否输入正确")
              }
            }
        )
      }
      else ElMessage("请检查账号密码是否输入正确")
      }


    return{
      user,clearData,submit,msg,setPasswordMsg,clickPassword
    }
  }
}
</script>

<style scoped>
p{
  color: red;
}
</style>