<template>
  <div class="page">
    <el-button @click="daochu">导出当天退订订单</el-button>
    <br>

    <el-table :data="filterTableData" style="width: 100%;height: 400px;margin-top: 100px" id="dctb">

      <el-table-column label="姓名" prop="name" />
      <el-table-column label="手机号" prop="phone" />
      <el-table-column label="身份证号" prop="idno" />
      <el-table-column label="订房时间" prop="checkintime" />
      <el-table-column label="结束时间" prop="endtime" />
      <el-table-column label="房间号" prop="roomno" />
      <el-table-column label="操作人" prop="operator" />
      <el-table-column label="订单金额" prop="money" sortable />


      <el-table-column align="right">
        <template #header>
          <el-input v-model="search" size="small" placeholder="查找"/>
        </template>
      </el-table-column>
    </el-table>

  </div>
</template>

<script>
import {computed, nextTick, onMounted, reactive, ref} from "vue";
import API from "@/axios/axiosinstan";
import XLXS from "xlsx";
import FileSaver from "file-saver";
import {ElMessage, ElMessageBox} from "element-plus";
export default {
  name:"Join",
  setup(){

    const filterTableData = computed(() =>
        data.table.filter(
            (data) =>
                !search.value ||
                data.name.toLowerCase().includes(search.value.toLowerCase())
        )
    )

    const  data = reactive({
      table:[]
    })

    let search = ref("")

    //  定义获取操作名单方法
    function getOp(){
      API({
              url:"http://localhost:8081/getOp",
              method:"get",
            }).then((res)=>{
                data.table = res.data
      },(err)=>{
            })
    }

    //定义删除操作名单
    function delectOp(){
      API({
              url:"http://localhost:8081/delectOp",
              method:"get",
            }).then((res)=>{

            },(err)=>{
            })
    }

    //刚挂载的时候
    onMounted(()=>{
      getOp();
    })

    //导出文件
    const exportClick = () => {

      //点击确认
      // 导出文件名
      const filename = new Date().toLocaleString()+"-"+localStorage.getItem("user")+".xlsx";
      // 导出表格加id,通过id获取要导出的表单
      const wb = XLXS.utils.table_to_book(document.getElementById("dctb"));
      const wbout = XLXS.write(wb, {
        bookType: "xlsx",
        bookSST: true,
        type: "array",
      });
      try {
        FileSaver.saveAs(
            new Blob([wbout], {
              type: "application/octet-stream",
            }),
            filename
        );
      } catch (e) {
        console.log(e);
      }



      return wbout;

    }



    //点击导出
    function daochu(){
      ElMessageBox.alert("是否要全部导出",{
        confirmButtonText:"确认",
        callback:(value, action)=>{
          if (value == 'confirm'){
            delectOp();
            data.table=[]
            exportClick()
          }
        }
      })


    }


    return{
      filterTableData,search,daochu
    }

  }
}





</script>

<style scoped>

.page{
  background-image: url("../image/3.jpg");
  width: 100%;
  height: 900px;
  font-size: 25px;

}
</style>