<template>


<div id="login" style="width: 100% ;height: 968px ; ">

  <el-dropdown>
    <el-button type="primary">
      账号管理<el-icon class="el-icon--right"><arrow-down /></el-icon>
    </el-button>
    <template #dropdown>
      <el-dropdown-menu>
        <el-dropdown-item @click="zhuce">账号注册</el-dropdown-item>
        <el-dropdown-item @click="xiugai">修改密码</el-dropdown-item>
      </el-dropdown-menu>
    </template>
  </el-dropdown>

<div class="ceter">
<!--  介绍标语-->
  <div class="center_introduce">
    <p><el-icon><Histogram /></el-icon>欢迎使用Xuefeng后台管理系统</p>
    <ul>
      <li><el-icon><Position /></el-icon>SpringBoot</li>
      <li><el-icon><Position /></el-icon>MybatisPlus</li>
      <li><el-icon><Position /></el-icon>Vue3</li>
      <li><el-icon><Position /></el-icon>ElementUi_Plus</li>
    </ul>
  </div>

<!--  用户提交数据表单-->
  <div class="center_from_user">
    <h2>登陆</h2> <br><br>

    <span>账号:</span>&nbsp;&nbsp;&nbsp;
    <el-input v-model="user.account" placeholder="Please input"  style="width: 300px ;height: 40px"/>
    <br><br>

    <span>密码:</span>&nbsp;&nbsp;&nbsp;
    <el-input
        v-model="user.password"
        type="password"
        placeholder="Please input password"
        show-password
        style="width: 300px;height: 40px"
    />
    <br><br>
    <el-button type="primary" round style="width: 380px ;height: 40px" @click="denglu">登陆</el-button>





  </div>

  <hr style="margin-top: 370px">
  Copyright ©2022-11 Xuefeng.vip All Rights Reserved

</div>

</div>


<!--  账号注册界面-->
  <el-dialog v-model="dialogzhuce" title="注册账号" style="text-align: center" width="600px" top="300px">
    <Register></Register>
  </el-dialog>


  <!--  密码修改界面界面-->
  <el-dialog v-model="dialogxiugai" title="修改密码" style="text-align: center" width="600px" top="300px">
    <modify></modify>
  </el-dialog>


</template>

<script>
import {reactive, ref} from "vue";
import API from "@/axios/axiosinstan";
import {ElMessageBox} from "element-plus";
import { ArrowDown } from '@element-plus/icons-vue'
import Register from "@/views/Register";
import Modify from "@/views/Modify";
import router from "@/router";
export default {
  name: "Login",
  components: {Modify, Register},

  setup(){

   //用户账号密码信息
  const user = reactive(
       {
         account:"",
         password:""
       }
   )


    //用户点击登陆
  function denglu(){
    //发送请求
    API({
      url:'http://localhost:8081/denglu',
      method:'post',
      data:user
    }).then((res)=>{
      console.log(res.data);
      //请求成功
     if (res.data.code == "成功"){
       //存储token
       localStorage.setItem("token","*********")
       //更新用户信息
       localStorage.setItem("user",user.account)
       //跳转主页面
       router.replace("/xuefeng")
     }else {
       ElMessageBox.alert( "账号密码有误","logging status")
     }
    },(err)=>{
      //请求失败
    });

  }

  //注册弹框是否弹出
  let  dialogzhuce = ref(false)

    //修改弹框是否弹出
    let  dialogxiugai = ref(false)

    //注册账号
    function zhuce(){
      dialogzhuce.value = true
    }

    //修改密码
    function xiugai() {
      dialogxiugai.value = true
    }





    return{
    user,denglu,ArrowDown,zhuce,xiugai,dialogzhuce,dialogxiugai
    }
  }
}
</script>




<style scoped>
.center_from_user h2{
  margin-top: 30px;

}
.center_introduce{
  float: left;
  height:300px ;
  width: 350px;

}
.center_from_user{
  float: left;
  height:350px ;
  width: 500px;

  text-align: center;
  background-color: white;
  border-radius: 20px;
}
.center_introduce p{
  color: yellow;
  font-size: 17px;
  position: relative;
  top: 50px;
  left: 50px;
}
.center_introduce ul li {
  margin-top: 10px;
  list-style: none;
}
.center_introduce ul{
  position: relative;
  top: 60px;
  left: 80px;
}
#login{
  background-image: url("../image/login.jpg");

}

.ceter{
  width: 900px;
  height: 410px;
  border: 1px solid rgba(200,200,200,0);
  position: relative;
  top: 200px;
  left: 600px;
}

.example-showcase .el-dropdown-link {
  cursor: pointer;
  color: var(--el-color-primary);
  display: flex;
  align-items: center;
}
</style>