

  <template>
    <div class="common-layout">
      <el-container>

<!--        头部-->
        <el-header height="80px" class="header" style="margin-left: -20px">

        <div class="introduce">
          <el-icon><House /></el-icon>
          Xue Feng
        </div>

          <div class="user">
            <br>
           <el-icon ><Avatar /></el-icon>&nbsp;&nbsp;&nbsp;<span>{{user}}</span> <br>
            <el-button @click="zhuxiao">注销</el-button>
          </div>

          </el-header>



        <el-container>



          <el-aside width="205px" class="cebian" >
            <el-col>
              <h5 style="font-size: 20px;margin-left: 50px">系统管理</h5>
              <el-menu
                  default-active="2"
                  class="el-menu-vertical-demo"
                  @open="handleOpen"
                  @close="handleClose"
              >


<!--                首页信息-->
                <el-menu-item index="2" @click="shouye">
                  <el-icon><icon-menu /></el-icon>
                  <span>首页</span>
                </el-menu-item>


<!--用户入住-->
                <el-menu-item index="2" @click="ruzhu">
                  <el-icon><icon-menu /></el-icon>
                  <span>用户入住</span>
                </el-menu-item>

 <!--订单管理-->
                <el-menu-item index="2" @click="dingdan">
                  <el-icon><icon-menu /></el-icon>
                  <span>订单管理</span>
                </el-menu-item>

                <!--交接-->
                <el-menu-item index="2" @click="jiaojie">
                  <el-icon><icon-menu /></el-icon>
                  <span>交班</span>
                </el-menu-item>






              </el-menu>
            </el-col>
          </el-aside>


<!--          分页内容-->
          <el-main style="padding-left: 0px;padding-top: 0px;padding-right: 0px">
            <router-view></router-view>
          </el-main>



        </el-container>
      </el-container>
    </div>
  </template>


<script>
import {
  Document,
  Menu as IconMenu,
  Location,
  Setting,
} from '@element-plus/icons-vue'
import router from "@/router";
import {onMounted} from "vue";

export default {
  name: "Xuefeng",
  setup(){
    //用户信息
    let user =  localStorage.getItem("user")

    //注销账号
    function zhuxiao(){
      localStorage.removeItem("token");
      localStorage.removeItem("user");
      router.replace("/login")
    }

    //点击首页,跳转页面
    function shouye(){
      router.push("/home")
    }

    //点击用户入住的时候
    function  ruzhu(){
      console.log("正在点击入住");
      router.push("/ruzhu")
    }

    //用户点击订单管理
    function dingdan(){
      router.push("/dingdan")
    }

    //用户点击交接
    function jiaojie(){
      router.push("/jiaojie")
    }

    //挂载的时候
    onMounted(()=>{
      //默认是首页
      router.push("/home")
    })

    return{
      user,zhuxiao,  Document,
     IconMenu,
      Location,
      Setting,shouye,ruzhu,
      dingdan,jiaojie
    }
  }
}
</script>

<style scoped>
span{
  font-size: 20px;
}
.cebian{
  height: 900px;
  background-color: #edeff0;
}
.header{
  background-color:  #a0cfff;
}

.introduce{
  height: 100% ;
  width: 205px ;
  font-size: 30px;
  font-weight: bold;
  color: white;
  text-align: center;
  line-height: 80px;
  background-color: #409EFF;
  float: left;
}
.user{
  height: 100% ;
  width: 200px ;
  float: left;
  margin-left: 1450px;
  text-align: center;

}
</style>