<template id="m">
<div class="page">
  <el-table :data="filterTableData" style="width: 100%;height: 400px;margin-top: 100px">

    <el-table-column label="姓名" prop="name" />
    <el-table-column label="手机号" prop="phone" />
    <el-table-column label="身份证号" prop="idno" />
    <el-table-column label="订房时间" prop="checkintime"  sortable/>
    <el-table-column label="结束时间" prop="endtime" sortable />
    <el-table-column label="房间号" prop="roomno" sortable />


    <el-table-column align="right">
      <template #header>
        <el-input v-model="search" size="small" placeholder="查找"/>
      </template>
      <template #default="scope">
        <el-button size="small" @click="handleEdit(scope.$index, scope.row)"
        >Edit</el-button
        >
        <el-button
            size="small"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
        >退订</el-button
        >
      </template>
    </el-table-column>
  </el-table>

</div>
</template>

<script>
import {computed, onMounted, reactive, ref} from "vue";
import API from "@/axios/axiosinstan";
import {ElMessage, ElMessageBox} from "element-plus";

export default {
  name: "Order",
  setup(){

    const search = ref('')

    const  tableDate = reactive({
      orderList:[]
    })

    const filterTableData = computed(() =>
        tableDate.orderList.filter(
                (data) =>
                !search.value ||
                data.name.toLowerCase().includes(search.value.toLowerCase())
        )
    )

    //获取订单的方法
    function getOrder(){
      API({
        url:"http://localhost:8081/getOrder",
        method:"get",
      }).then((res)=>{
       tableDate.orderList = res.data
      })
    }

    //删除订单,同时更新操作人表
    function delectOrder(row){
      API({
              url:"http://localhost:8081/delectOrder",
              method:"post",
              data:[{roomno:row.roomno},{op:localStorage.getItem("user")}]
            }).then((res)=>{
              ElMessage("删除成功")
             getOrder()
            },(err)=>{
            })
    }

    onMounted(()=>{
      getOrder()
    })


    //点击编辑
    function handleEdit(index,row){
      console.log(index);
      console.log(row);
    }

    //点击退订
    function handleDelete(idnex,row){

      ElMessageBox.alert("是否退订",{
        confirmButtonText:"确认",
        callback:(value, action)=>{
          if (value == "confirm"){
             delectOrder(row)
          }

        }
      })


    }

    return{
      search ,filterTableData,handleEdit,handleDelete
    }
  }

}
</script>

<style scoped>

.page{
  background-image: url("../image/3.jpg");
  width: 100%;
  height: 900px;
  font-size: 25px;
  border:1px solid white;

}
</style>